.navbar{
    position: sticky;
    top: 0%;
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    justify-content: space-evenly;
    border-bottom: 0.1px solid lightgray;
    z-index: 999;
    height: fit-content;
/* height: 40px; */
}
